.sendForm {
  display: flex;
  gap: 44px;
}

.textarea {
  background: #f5f7fb;
  box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03),
    0px 1px 2px rgba(29, 33, 38, 0.1);
  border-radius: 20px;
  border: none;
  outline: none;
  font-size: 30px;
  width: 976px;
  height: 40px;

  opacity: 0.9;

  color: rgba(0, 0, 0, 0.6);
  font-family: Montserrat;
  font-size: 35px;
}

.button {
  border-radius: 25px;
  border: none;
  outline: none;
  background-color: #0066cc;
  font-size: 30px;
  width: 120px;
  height: 40px;

  color: #fff;

  font-size: 17px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

